.brands-card {
  @include screen(laptop) {
    display: flex;
    align-items: center;
    gap: 30px;
  }
  &-reverse {
    @include screen(laptop) {
      display: flex;
      flex-direction: row-reverse;
    }
  }
  &__content {
    margin-top: 22px;
    @include screen(tablet) {
      margin-top: 24px;
      display: grid;
      grid-template-columns: 258px 1fr;
      column-gap: 33px;
    }
    @include screen(laptop) {
      display: flex;
      flex-direction: column;
      margin-top: 0;
    }
  }
  &__title {
    font-family: 'Tenor Sans';
    font-size: 16px;
    line-height: 21px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    @include screen(tablet) {
      grid-row: 1 / span 1;
    }
    @include screen(laptop) {
      font-size: 20px;
      line-height: 20px;
      order: 1;
    }
  }
  &__text {
    margin-top: 16px;
    font-size: 14px;
    line-height: 18px;
    @include screen(tablet) {
      grid-row: 2 / span 2;
    }
    @include screen(laptop) {
      font-size: 16px;
      line-height: 20.8px;
      order: 2;
      max-width: 380px;
      margin-top: 14px;
    }
  }
  &__quality {
    margin-top: 34px;
    border: 1px solid $black-400;
    padding: 12px 18px;
    width: 100%;
    display: flex;
    align-items: center;
    gap: 10px;
    @include screen(tablet) {
      grid-row: 4 / span 2;
      margin-top: 16px;
      padding: 13px 7px;
    }
    @include screen(laptop) {
      order: 4;
      margin-top: 60px;
      width: max-content;
      padding: 17px 32px;
    }
    svg {
      width: 45px;
      height: 20px;
      min-width: 45px;
      min-height: 20px;
      @include screen(tablet) {
        width: 30px;
        height: 13px;
        min-width: 30px;
        min-height: 13px;
      }
      @include screen(laptop) {
        width: 90px;
        height: 40px;
        min-width: 90px;
        min-height: 40px;
      }
    }
    span {
      font-size: 14px;
      line-height: 18px;
      opacity: 0.8;
      @include screen(tablet) {
        font-size: 12px;
        line-height: 15px;

      }
      @include screen(laptop) {
        font-size: 16px;
        line-height: 20.8px;
        max-width: 290px;
      }
    }
  }
  &__items {
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    row-gap: 16px;
    column-gap: 22px;
    @include screen(tablet) {
      margin-top: 16px;
      grid-column: 2 / span 1;
      grid-row: 2 / span 6;
    }
    @include screen(laptop) {
      margin-top: 38px;
      column-gap: 88px;
      row-gap: 20px;
      max-width: 546px;
      order: 3;
    }
  }
  &__item {
    svg {
      width: auto;
      height: 36px;
      @include screen(laptop) {
        width: auto;
        height: 54px;
      }
    }
    span {
      display: inline-block;
      margin-top: 10px;
      font-size: 14px;
      line-height: 18px;
      opacity: 0.8;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 20.8px;
      }
    }
  }
  &__button {
    margin-top: 34px;
    display: inline-block;
    @include screen(tablet) {
      grid-row: 6 / span 2;
      margin-top: 24px;
    }
    @include screen(laptop) {
      order: 5;
      margin-top: 38px;
    }
  }
  &__sliders {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    @include screen(tablet) {
      flex-direction: row;
      display: flex;
      align-items: stretch;
      grid-template-columns: 551px 167px;
    }
    @include screen(laptop) {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 10px;
      max-width: 685px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .swiper {
      width: 100%;
      height: 100%;
      @include screen(tablet) {
        width: auto;
      }
      @include screen(laptop) {
        width: 100%;
      }
    }
  }
  &__slider {
    @include screen(tablet) {
      min-width: 551px;
    }
    .swiper-slide {
      overflow: hidden;
      @include screen(tablet) {
        height: 336px;
      }
      @include screen(laptop) {
        height: 486px;
      }
    }
  }
  &__thumbs {
    max-height: 140px;
    overflow: hidden;
    @include screen(tablet) {
      min-width: 167px;
      max-height: 336px;
    }
    .swiper-slide {
      cursor: pointer;
      @include screen(tablet) {
        min-height: 105px;
      }
      @include screen(laptop) {
        height: 140px;
        overflow: hidden;
      }
    }
  }
  .button {
    @include screen(tablet) {
      width: 150px;
    }
    @include screen(laptop) {
      width: 162px;
    }
  }
}