
.nav {
  margin-top: -1px;
  display: grid;
  grid-template-columns: 3fr 1fr;
  background-color: $white;
  position: relative;
  border-top: 1px solid rgba($gray, 0.51);
  @include screen(laptop) {
    margin-top: 5px;
    overflow-y: auto;

    grid-template-columns: 404px 1fr 540px;
    width: 100%;
    height: calc(100vh - 73px);
  }
  &__title {
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 500;
    line-height: 18px;
    letter-spacing: 0.02em;
    padding: 10px 20px;
    border-bottom: 1px solid rgba($gray, 0.51);
    border-right: 1px solid rgba($gray, 0.51);
    @include screen(laptop) {
      padding: 30px 30px 20px 20px;
      font-size: 16px;
      line-height: 21px;
    }
  }
  &__column {
    height: calc(100vh - 146px);
    overflow-y: auto;
    display: grid;
    &::-webkit-scrollbar {
      width: 10px;
      background-color: $gray-100;
    }
    &::-webkit-scrollbar-thumb {
      background-color: $black-400;
    }
    &-two {
      grid-template-columns: repeat(3, minmax(0, 1fr));
      @include screen(laptop) {
        grid-template-columns: 180px 1fr;
      }
    }
    &-three {
      grid-template-columns: repeat(3, minmax(0, 1fr));
      border-bottom: 1px solid rgba($gray, 0.51);
      @include screen(laptop) {
        border-bottom: none;
      }
    }
  }
  &__block {
    &-three {
      grid-column: span 2;
      .nav__title {
        background-color: $white;
        border-top: 1px solid rgba($gray, 0.51);
      }
      @include screen(laptop) {
        grid-column: span 1;
        .nav__title {
          border-top: none;
        }
      }
    }
  }
  &-item {
    height: 100%;
    border-right: 1px solid rgba($gray, 0.51);
    &__link {
      color: $black-400;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 20px;
      }
    }
    &-tablet {
      display: flex;
      @include screen(laptop) {
        display: none;
      }
      .nav-item__title-top {
        border-top: none;
      }
    }

    &__title {
      border-bottom: 1px solid rgba($gray, 0.51);
      padding: 10px 20px;
      position: relative;
      span {
        white-space: nowrap;
        font-size: 14px;
        font-weight: 500;
        line-height: 18px;
        position: relative;
        &:before {
          position: absolute;
          content: '';
          bottom: 3px;
          left: 0;
          width: 100%;
          height: 1px;
          background-color: $black-400;
        }
        @include screen(laptop) {
          font-size: 16px;
          line-height: 21px;
          text-transform: none;
        }
      }

      @include screen(laptop) {
        padding: 10px 10px 10px 20px;
      }
      &-top {
        border-top: 1px solid rgba($gray, 0.51);
      }
    }
    &__category {
      display: flex;
      flex-direction: column;
      width: 100%;
      &-bottom {
        @include screen(laptop) {
          padding-bottom: 50px;
          border-bottom: 1px solid rgba($gray, 0.51);
        }
      }
      &-desktop {
        display: none;
        @include screen(laptop) {
          display: flex;
        }
      }
    }
    &__links {
      padding: 10px 20px;
      display: flex;
      flex-direction: column;
      gap: 16px;
      max-width: max-content;
      width: 100%;
      @include screen(laptop) {
        gap: 13px;
        padding: 10px 10px 10px 20px;
      }
    }
    &__link {
      text-transform: none;
    }
  }
}