.heading {
  &__wrapper {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__title {
    font-family: 'Tenor Sans';
    font-size: 20px;
    font-weight: 400;
    line-height: 26px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    max-width: 272px;
    @include screen(tablet) {
      font-size: 24px;
      line-height: 31px;
      max-width: 524px;
    }
    @include screen(laptop) {
      font-size: 28px;
      line-height: 36px;
      max-width: 618px;
    }
  }
  &__time {
    font-size: 16px;
    font-weight: 400;
    line-height: 21px;
    color: $gray;
    opacity: 0.51;
    display: none;
    @include screen(tablet) {
      display: block;
    }
  }
}
