.brand-hero2 {
  .brand-hero {
    &__bg {
      cursor: default;
      picture {
        &:before {
          display: none;
        }
      }
    }
    &__content {
      @include screen(tablet) {
        margin-top: 300px;
      }
      @include screen(laptop) {
        margin-top: 0;
      }
    }
    &__text {
      @include screen(tablet) {
        max-width: 248px;
      }
      @include screen(laptop) {
        max-width: 300px;
      }
    }
  }
}