.news-hero {
  padding-top: 16px;
  @include screen(tablet) {
    padding-top: 24px;
  }
  &__wrapper {
    display: flex;
    flex-direction: column;
    gap: 10px;
    @include screen(laptop) {
      flex-direction: row;
    }
  }
  &__items {
    display: flex;
    gap: 10px;
    @include screen(tablet) {
      display: grid;
      grid-template-columns: repeat(3, minmax(0, 1fr));
    }
    @include screen(laptop) {
      grid-template-columns: repeat(1, minmax(0, 1fr));
      grid-template-rows: repeat(3, minmax(0, 1fr));
    }
  }
  &__item {
    &:last-child {
      display: none;
      @include screen(tablet) {
        display: block;
      }
      @include screen(laptop) {
        display: flex;
      }
    }
    @include screen(laptop) {
      display: flex;
      gap: 10px;
    }
  }
  &__banner {
    position: relative;
    max-height: 213px;
    width: 100%;
    overflow: hidden;
    @include screen(tablet) {
      max-height: 463px;
    }
    @include screen(laptop) {
      max-height: 563px;
      max-width: 884px;
    }
    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
      object-position: center center;
    }
  }
  &__name {
    position: absolute;
    bottom: 10px;
    left: 15px;
    font-family: 'Tenor Sans';
    font-size: 20px;
    font-weight: 400;
    line-height: 26px;
    letter-spacing: 0.02em;
    max-width: 113px;
    color: $white;
    text-transform: uppercase;
    @include screen(tablet) {
      left: 20px;
      bottom: 15px;
      font-size: 24px;
      line-height: 31.2px;
      max-width: max-content;
    }
    @include screen(laptop) {
      bottom: 30px;
      left: 40px;
      font-size: 28px;
      line-height: 36.4px;
    }
  }
  &__img {
    height: 221px;
    @include screen(tablet) {
      height: 155px;
    }
    @include screen(laptop) {
      width: 276px;
      height: 181px;
      min-width: 276px;
      min-height: 181px;
    }
    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
      object-position: center center;
    }
  }
  &__title {
    margin-top: 13px;
    font-family: 'Tenor Sans';
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical; 
    @include screen(tablet) {
      margin-top: 10px;
    }
    @include screen(laptop) {
      margin-top: 0;
      font-size: 16px;
      line-height: 21px;
    }
  }
  &__text {
    margin-top: 10px;
    font-family: 'Jost';
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    color: rgba($black-400, 0.6);
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    @include screen(laptop) {
      margin-top: 27px;
      font-size: 16px;
      line-height: 21px;
    }
  }
}