.main-news {
  overflow: hidden;
  padding: 30px 0;
  @include screen(laptop) {
    padding: 50px 0;
  }
  &__title {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__link {
    display: flex;
    align-items: center;
    gap: 10px;
    span {
      font-size: 16px;
      line-height: 21px;
      color: $black-400;
      transition: color $transition-default;
    }
    svg {
      width: 25px;
      transition: width $transition-default;
      path {
        fill: $black-400;
        transition: fill $transition-default;
      }
    }
    @include screen(laptop) {
      &:hover {
        span {
          color: $black-hover;
        }
        svg {
          width: 53px;
          path {
            fill: $black-hover;
          }
        }
      }
    }
  }
  &__image {
    img {
      width: 100%;
      aspect-ratio: 1 / 1;
      height: 100%;
    }
  }
  h3 {
    font-family: 'Tenor Sans';
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.02em;
    margin-top: 16px;
    text-transform: uppercase;
    @include screen(tablet) {
      margin-top: 15px;
      max-width: 100%;
    }
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
      margin-top: 20px;
      max-width: 300px;
    }
  }
  &__description {
    margin-top: 5px;
    font-size: 14px;
    line-height: 18px;
    opacity: 0.6;
    color: $gray-700;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;  
    overflow: hidden;
    @include screen(tablet) {
      margin-top: 10px;
      max-width: 100%;
      -webkit-line-clamp: 2;
    }
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
      -webkit-line-clamp: 4;
    }
    @include screen(desktop) {
      max-width: 400px;
    }
  }
  .swiper {
    overflow: visible;
    margin-top: 16px;
    @include screen(tablet) {
      margin-top: 24px;
    }
  }
}